















































import Vue from "vue";
import { localUserStatus } from "../util";

export default Vue.extend({
  data() {
    return {
      isShowMenu: false,
      path: window.location.pathname
    };
  },
  created() {
    this.awaitGetData();
  },
  methods: {
    // string 数组首字母大写
    firstUpperCase(str: string): string {
      return str.toLowerCase().replace(/( |^)[a-z]/g, L => L.toUpperCase());
    },

    menuClick() {
      this.isShowMenu = !this.isShowMenu;
    },

    async awaitGetData() {
      await localUserStatus();
    },

    getRouterAux(): any {
      const routerAux = new Map();
      const params = this.$route.params;
      const query = this.$route.query;
      routerAux.set("home", {
        routerName: "Home",
        showName: "Home"
      });
      routerAux.set("commit", {
        routerName: "Commit",
        params,
        query,
        showName: "Commit"
      });
      routerAux.set(params.domain, {
        routerName: "ManageRepos",
        showName: params.domain
      });
      routerAux.set(params.repo, {
        routerName: "Repo",
        params,
        query,
        showName: params.repo
      });
      routerAux.set("new", {
        routerName: "New",
        showName: "New"
      });
      routerAux.set("login", {
        routerName: "Login",
        showName: "Login"
      });
      return routerAux;
    }
  },
  computed: {
    mapRouter() {
      let result: any[] = this.breadcrumbData;
      let routers: Map<string, any> = this.getRouterAux();
      return result.map(v => routers.get(v));
    },
    breadcrumbData(): string[] {
      let result: string[] = this.path
        .split("/")
        .filter(s => s != "" && s > "9");
      result.unshift("home");
      return result;
    }
  }
});
